.cart{
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  border: none;
  padding: 10px;
  transition: 0.3s all ease-in;
}
.cart:hover{
  background-color: #000;
  color: #fff;
}
nav{
  display: flex;
  padding: 10px;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  background-color:#01010C ;
}
.rate{
  margin-left: 30px;

}
.icon{
  /* position: relative; */
  margin-right: 20px;
}
.brand{
  margin-left: 20px;
}
.p1{
  position: relative;
}
.p1:after{
  position:absolute;
  right:35%;
  top:8%;
  content: attr(data-count);
  font-size:50%;
  padding:.2em;
  border-radius:50%;
  line-height:1em;
  color: white;
  background:rgba(255,0,0,.85);
  text-align:center;
  min-width: 1em;
font-weight:bold;
}
.cartItem{
  padding: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.cart-title{
  text-align: center;
  padding: 20px;
}
.incdec{
  display: flex;
  flex-direction: column;
  
}
.incdec .arrow{
 cursor: pointer; 
}
.itemName{
  width: 50%;
}
.incdec button{
  background-color: #fff;
  border: none;
}
.price-rating{
  width: 100%;
  display: flex;
  justify-content: space-between;
 
}